import React from 'react';
import PropTypes from 'prop-types';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import Tooltip from 'material-ui/Tooltip';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import { navigateTo } from "gatsby-link"

import { withStyles } from 'material-ui/styles';
import grey from 'material-ui/colors/grey';
import withRoot from '../../withRoot';
import logoPath from '../../../public/img/site-logo.png';

const styles = theme => ({
  flex: {
    flex: 1,
    paddingLeft: 48,
  },
  logo: {
    height: '48px',
    cursor: 'pointer',
    '&:hover': {
      '-webkit-filter': 'invert(100%) !important', // chrome, safari
      'filter:': 'invert(100%) !important;', // firefox
    },
  },
  navButton: {
    height: 64,
    marginLeft: -12,
    marginRight: 20,
    color: grey[500],
    textTransform: 'none',
    fontSize: '1em',
  },
});


class Navbar extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <AppBar position="static" elevation={0}>
        <Toolbar>
          <Typography variant="title" color="inherit" className={classes.flex}>
            <img className={classes.logo} src={logoPath} onClick={() => navigateTo('/')}/>
          </Typography>
          <Button className={classes.navButton}
            onClick={() => navigateTo('/china18')}>
            China 2018
          </Button>
          <Tooltip id="tooltip-blog" title="Coming Soon">
            <Button className={classes.navButton}>
              Blog
            </Button>
          </Tooltip>
          <Tooltip id="tooltip-support" title="Coming Soon">
            <Button className={classes.navButton}>
              Support Us
            </Button>
          </Tooltip>
          <Button className={classes.navButton}
            onClick={() =>
              window.open('https://aivillage.slack.com/join/shared_invite/enQtMzIyNzcwNDE2MjQzLWIwYmM5N2UzYjYyMWM3YzRmZjNlYjU2MWY5ODM0ZWIxNTJlMzViZDAyNTAzOThiNjQ2ZjY4NzZmN2NlNmEyZDE', '_blank')
            }>
            Join Us
          </Button>
        </Toolbar>
      </AppBar>
    );
  }
}

Navbar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(Navbar));


